* {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1rem;
}

body {
  background: black;
  color:aliceblue;
  padding: 60px 30px;
}

@media only screen and (min-device-width : 768px) {
  body {
    padding: 60px;
  }
}

input {
  color: mediumslateblue;
  font-size: 1.3rem;
  width: 100%;
}

.card-grid {
  display: grid;
  gap: .5%;
  grid-template-columns: 1fr;
  margin-bottom: 5%;
}

@media only screen and (min-device-width : 768px) {
  .card-grid {
    gap: 1.5% 1%;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media only screen  and (min-width : 1224px) {
  .card-grid {
    grid-template-columns: repeat(5, 1fr);
  }
}

.card {
  background: lightgray;
  border: 1px solid gray;
  border-radius: 6px;
  color: black;
  display: flex;
  flex-wrap: wrap;
}

.card p {
  font-weight: 500;
  margin: 3px 0;
  padding: 1rem;
  text-align: center;
}

.card img {
  width: 100%;
}
